    export default {
        mounted(){
        	 var self = this;
             document.getElementById("ximgage").onload = function () {
			      self.showimg();
			 }
        },
        props: ['imagesrc'],
        data () {
            return {
                showloading:true,
                showpic:false
            }
        },
        methods:{
            'doclosesr':function(){
                this.$emit('closesr', false);
            },
            'showimg':function(){
                this.showloading =false ;
                this.showpic =true ;
            }
        }
    }



